.MainDiv {
  display: grid;
  grid-template-rows: 2fr 10fr;
  background-color: rgb(128, 128, 128);
}

.Header {
  padding: 35px;
  color: white;
  text-align: center;
  font-family: "Courier New", Courier, monospace;
  font-size: xx-large;
}

.AllContent {
  display: grid;
  grid-template-columns: 2fr 10fr;
  background-color: rgb(255, 255, 255);
  text-align: center;
}

.NavBar {
  padding: 10px;
}

.Content {
  padding: 10px;
}
